import {
  BulletWrapper,
  Center,
  CenterContainer,
  CenteredImages,
  DescriptionHeader,
  Images,
  Left,
  Li,
  PrecisedDescription,
  ProductDescription,
  Right,
  SectionWrapper,
  StyledLine,
  SubHeader,
  Ul,
  UnderlineWrapper,
} from "./styled";

import leftImage from "../../images/leftImage.png";
import rightImage from "../../images/rightImage.png";
import centerLeft from "../../images/center1.png";
import centerRight from "../../images/center2.png";
import bulletFirst from "../../images/bullet1.png";
import bulletSecond from "../../images/bullet2.png";

export const Main = () => {
  return (
    <>
      <SectionWrapper>
        <SubHeader id="about">Co wyrnia nasz preparat</SubHeader>
        <StyledLine />
      </SectionWrapper>
      <ProductDescription>
        <Left>
          <DescriptionHeader>
            Innowacyjny dodatek - Czarciego Pazura
            <StyledLine />
            <PrecisedDescription>
              Zapewnia dodatkowe wsparcie w zwalczaniu stanw blowych i zapalnych. Skoncentrowane
              skadniki aktywne, opracowane we wsppracy z lekarzami weterynarii, przynosz szybkie
              i zauwaalne efekty, szczeglnie korzystne dla zwierzt z powanymi problemami
              stawowymi.
            </PrecisedDescription>
          </DescriptionHeader>
          <UnderlineWrapper>
            <Images src={leftImage} alt="special ingredient" />
          </UnderlineWrapper>
        </Left>
        <Center>
          <DescriptionHeader>
            Wygoda -<br />
            dwa sposoby podawania
            <StyledLine />
            <PrecisedDescription>
              <Ul>
                <Li>
                  <BulletWrapper src={bulletFirst} />
                  Bezporednio do pyszczka
                </Li>
                <Li>
                  <BulletWrapper src={bulletSecond} />
                  Zmieszany z karm
                </Li>
              </Ul>
            </PrecisedDescription>
          </DescriptionHeader>
          <CenterContainer>
            <CenteredImages src={centerLeft} alt="instruction" />
            <CenteredImages src={centerRight} alt="instruction" />
          </CenterContainer>
        </Center>
        <Right>
          <DescriptionHeader>
            Skuteczno -<br /> skoncentrowana dawka
            <StyledLine />
            <PrecisedDescription>
              <strong>Tylko jedna porcja dziennie.</strong>
              <br /> To nie tylko wygoda, ale rwnie gwarancja, e Twoje zwierz otrzymuje
              wszystko, czego potrzebuje, aby cieszy si zdrowiem i aktywnoci na dugo.
              Skorzystaj z zalet Arthrohard i zobacz rnic w komforcie ycia swojego zwierzcia.
            </PrecisedDescription>
          </DescriptionHeader>
          <UnderlineWrapper>
            <Images src={rightImage} alt="dosage" />
          </UnderlineWrapper>
        </Right>
      </ProductDescription>
    </>
  );
};
