import {
  BuyNowButton,
  Container,
  ContentBox,
  Description,
  Details,
  FlexContainer,
  Header,
  HeaderWrapper,
  ImageWrapper,
  IngredientsGlobalWrapper,
  LeftSide,
  Parallax,
  ParallaxWrapper,
  RightSide,
  StyledEllipse,
  StyledLine,
  SubHeader,
  Text,
  Title,
} from "./styled";

import medicineIngredients from "../../images/medicineIngredients.png";
import dog from "../../images/dog.png";
import { useState } from "react";

export const Ingredients = () => {
  const [mouseX, setMouseX] = useState(0);

  const handleMouseMove = (event) => {
    setMouseX(event.clientX);
  };
  return (
    <>
      <IngredientsGlobalWrapper>
        <Container>
          <HeaderWrapper>
            <Header id="ingredients">Skad w 5 ml</Header>
            <StyledLine />
            <SubHeader>Co daj poszczeglne skadniki:</SubHeader>
          </HeaderWrapper>
        </Container>
        <FlexContainer>
          <ContentBox>
            <LeftSide>
              <StyledEllipse />
              <Description>
                <Title>Siarczan glukozaminy</Title>
                <Text>650 mg</Text>
                <Details>
                  Glukozamina jest naturalnym skadnikiem chrzstki. Dodatek glukozaminy przyczynia
                  si do zwikszenia produkcji glikozaminoglikanw (m.in. siarczanu keratanu,
                  heparanu, kwasu hialuronowego), ktre pomagaj w odbudowie chrzstki, co jest
                  szczeglnie korzystne dla zwierzt ze zmianami zwyrodnieniowymi staww.
                </Details>
              </Description>
              <StyledEllipse />
              <Description>
                <Title>Siarczan chondroityny</Title>
                <Text>300 mg</Text>
                <Details>
                  Chondroityna, podobnie jak glukozamina, jest skadnikiem chrzstki. Chondroityna
                  pomaga zwalcza enzymy, ktre niszcz chrzstk, a take pomaga chrzstce
                  zatrzyma wod, co jest wane dla jej sprystoci i absorpcji wstrzsw.
                </Details>
              </Description>
              <StyledEllipse />
              <Description>
                <Title>Kwas hialuronowy</Title>
                <Text>50 mg</Text>
                <Details>
                  Kwas hialuronowy jest kluczowym skadnikiem pynu stawowego, ktry dziaa jak smar
                  i amortyzator dla staww. Suplementacja kwasem hialuronowym moe pomc w
                  utrzymaniu prawidowej objtoci i konsystencji pynu stawowego.
                </Details>
              </Description>
            </LeftSide>
          </ContentBox>
          <ContentBox>
            <ImageWrapper src={medicineIngredients} />
            <BuyNowButton>Kup teraz</BuyNowButton>
          </ContentBox>
          <ContentBox>
            <RightSide>
              <StyledEllipse />
              <Description>
                <Title>Czarci pazur</Title>
                <Text>250 mg</Text>
                <Details>
                  Czarci pazur jest rolin, ktra ma waciwoci przeciwzapalne i przeciwblowe.
                  Moe to pomc zwierztom, ktre dowiadczaj blu i stanu zapalnego zwizanego z
                  problemami ze stawami.
                </Details>
              </Description>
              <StyledEllipse />
              <Description>
                <Title>Witamina C</Title>
                <Text>50 mg</Text>
                <Details>
                  Witamina C jest potnym przeciwutleniaczem, ktry moe pomc w ochronie staww
                  poprzez neutralizacj wolnych rodnikw, ktre mog uszkadza chrzstk. Ponadto,
                  witamina C odgrywa wan rol w produkcji kolagenu, kluczowego skadnika
                  chrzstki.
                </Details>
              </Description>
              <StyledEllipse />
              <Description>
                <Title>MSM</Title>
                <Text>50 mg</Text>
                <Details>
                  Metylosulfonylometan, czyli MSM, to naturalny zwizek siarki, ktry pomaga w
                  utrzymaniu zdrowych staww poprzez zmniejszenie stanu zapalnego i blu. MSM moe
                  rwnie pomaga w produkcji kolagenu, co przyczynia si do zdrowia chrzstki
                  stawowej.
                </Details>
              </Description>
            </RightSide>
          </ContentBox>
        </FlexContainer>
        <ParallaxWrapper onMouseMove={handleMouseMove}>
          <Parallax
            src={dog}
            style={{
              transform: `translateX(${(mouseX - window.innerWidth / 2) / 30}px)`,
            }}
          />
        </ParallaxWrapper>
      </IngredientsGlobalWrapper>
    </>
  );
};
